import Vue from 'vue'
import Web3 from 'web3'

let iweb3 = '';
const web3 = new Web3(new Web3.providers.WebsocketProvider('wss://mainnet.infura.io/ws/v3/b0b1a6d88e734ab2a126d672e435a94b') );
// const web3 = new Web3(window.ethereum);

if(window.ethereum){
	iweb3 = new Web3(window.ethereum);
}else{
	iweb3 = new Web3(new Web3.providers.HttpProvider('http://127.0.0.1:4321'));
}

Vue.prototype.$web3 = web3

class Contract {
    constructor(chainId) {
        this.chainId = chainId
    }

    set(chainId) {
        this.chainId = chainId
    }

    get(key) {
        const contract = require(`@/assets/jsons/${key}.json`)
        const storage = require(`@/assets/jsons/${key}Storage.json`)
        return new web3.eth.Contract(contract.abi, storage.networks[this.chainId].address)
    }
	
	getI(key) {
	    const contract = require(`@/assets/jsons/${key}.json`)
		const storage = require(`@/assets/jsons/${key}Storage.json`)
		return new iweb3.eth.Contract(contract.abi, storage.networks[this.chainId].address)
	}
}

export default async callback => {
    Vue.prototype.$contract = new Contract(await web3.eth.net.getId())
	
	if(window.ethereum){
		if(window.ethereum.isMetaMask){
			ethereum.enable().then(async function (accounts) {
				callback(accounts[0])
				
				ethereum.on('accountsChanged', function (res) {
					if(res[0]!=accounts[0]){
						window.location.reload()
					}
				})
			}).catch(function (reason) {
				callback('')
			})
		}else if(window.ethereum.isTrust){
			callback(window.ethereum.address)
		}else{
			callback(window.ethereum.selectedAddress)
		}
	}else{		
		iweb3.eth.getAccounts()
		.then(result=>{
			callback(result[0])
		})
		.catch(err=>{
			callback('')
		});
	}
}

Vue.prototype.toTime = (times , type) => {
	function add(m){return m<10?'0'+m:m }
	var time = new Date(times * 1000);
	var y = time.getFullYear();
	var m = time.getMonth()+1;
	var d = time.getDate();
	var h = time.getHours();
	var mm = time.getMinutes();

	if (type === 'Y-M-D') {
		return y+'-'+add(m)+'-'+add(d);
	} else if (type === 'Y-M-D H:i') {
		return y+'-'+add(m)+'-'+add(d)+' '+add(h)+':'+add(mm);
	}
}

Vue.prototype.BN = (num) => {
	var BN = new web3.utils.BN(num); 
	return BN
}

Vue.prototype.currentTime = async (type = 'current') => {
	const time = (await web3.eth.getBlock('latest').then()).timestamp
	if(type == 'eight'){
		// return new Date(new Date().toLocaleDateString()).getTime() / 1000 + (8 * 3600)
		return new Date(new Date(time*1000).toLocaleDateString()).getTime() / 1000 + (8 * 3600)
	}else{
		// return new Date().getTime() / 1000
		return time;
	}
}

String.prototype.toFixed = function(decimal) {
    const index = this.indexOf('.') + 1
    
    if (index > 0) {
        let suffix = this.slice(index)
        const len = this.slice(index).length

        if (decimal == 0) {
            return this.slice(0, index - 1)
        }else if(len > decimal || len == decimal){
			return this.slice(0, index + decimal)
		}
        
        return `${this.slice(0, index)}${suffix}${Array(decimal - len + 1).join(0)}`
    }

    return `${this}.${Array(decimal + 1).join(0)}`
}

/* 字符串从unit转换为wei
 * @method toWei
 * @param {string} unit 单位;默认为ether
 * @return {string} 返回wei
 */
String.prototype.toWei = function(unit = 'ether') {
	return toWei(this, unit)
}

/* 字符串从wei转换到uint
 * @method fromWei
 * @param {string} unit 单位;默认为ether
 * @return {string} 返回wei
 */
String.prototype.fromWei = function(unit = 'ether') {
	return fromWei(this, unit)
}

/* 数字从unit转换为wei
 * @method toWei
 * @param {string} unit 单位;默认为ether
 * @return {string} 返回wei
 */
Number.prototype.toWei = function(unit = 'ether') {
	return toWei(`${this}`, unit)
}

/* 数字从wei转换到uint
 * @method fromWei
 * @param {string} unit 单位;默认为ether
 * @return {string} 返回wei
 */
Number.prototype.fromWei = function(unit = 'ether') {
	return fromWei(`${this}`, unit)
}

function toWei(val, unit) {
	return web3.utils.toWei(val, unit)
}

function fromWei(val, unit) {
	return web3.utils.fromWei(val, unit)
}